<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()">
          <span>返回</span>
        </div>
        <span>选择联系人</span>
        <span style="position: absolute;right:15px;top:0;">
          <van-button type="primary" size="small" @click="confirm">确定 ({{result.length}})</van-button>
        </span>
      </div>
    </header>
    <!-- <van-row class="friendList">
      <van-col span="3" v-for="(item,index) in result" :key="index">
        <div>
          <img :src="'/api/file/showImg?fileUrl='+item.user.headerUrl" alt class="imgStyle" />
        </div>
      </van-col>
    </van-row>-->

    <van-checkbox-group v-model="result">
      <van-cell-group>
        <van-cell v-for="(item, index) in friendList" clickable :key="index" @click="toggle(index)">
          <img
            :src="'/api/file/showImg?fileUrl='+item.user.headerUrl"
            alt
            style="width:40px;height:40px;"
          />
          <span>{{item.user.nickname}}</span>
          <van-checkbox :name="item.user.userId" ref="checkboxes" slot="right-icon" />
        </van-cell>
      </van-cell-group>
    </van-checkbox-group>
  </div>
</template>
<script>
import { getMessage, send } from "../../socket/socket";
import qs from "qs";
export default {
  data() {
    return {
      result: [this.$route.query.userId],
      friendList: [],
      userInfo: ""
    };
  },
  created() {
    this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
    this.getFriendList();
  },

  methods: {
    //好友列表
    getFriendList() {
      this.$http.get("/api/friend/getFriendList").then(res => {
        if (res.data.code == 200) {
          //   console.log(res);
          this.friendList = res.data.data;
        }
      });
    },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    //确认拉人
    confirm() {
      console.log(this.result);
      let obj = this.result.join(",");
      let pullers = {
        pullers: obj
      };
      if (this.result.length > 1) {
        this.$http
          .post("/api/chat/creatGroupChatRoom", qs.stringify(pullers))
          .then(res => {
            // console.log(res);
            if (res.data.code == 200) {
              let params = {
                roomId: res.data.data.roomId,
                roomType: "group",
                userId: this.userInfo.userId,
                top: false,
                lastMsgId: null,
                limit: 2
              };
              this.$router.push({ path: "/wechat/dialogue", query: params });
            }
          });
      }
    }
  },
  mounted() {
    // console.log(this.$route.query);
  }
};
</script>
<style>
.dialogue-info {
  background: white;
}
.imgStyle {
  width: 40px;
  height: 40px;
  border-radius: 5px;
}
.friendList {
  background: white;
  padding: 10px;
}
</style>


